import React, { FC, ReactElement, useState } from "react";
import axios from "axios";
import Container from "../../components/ContainerComponents/Container";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import { Auth } from "../../actions/auth.action";
import TextInput from "../../components/Inputs/TextInput";
import { AcademicYearDetailsInterfaceForm } from "../../types/AcademicTypes";
import { setAxiosToken } from "../../utils/AxiosToken";
import { APP_URL } from "../../config/app.config";

type AddNewAcademicFormProps = {
  auth: Auth;
};

const _AddNewAcademicForm: FC<AddNewAcademicFormProps> = ({
  auth,
}): ReactElement => {
  const [formData, setFormData] = useState<AcademicYearDetailsInterfaceForm>({
    academic_year_name: "",
    academic_year_description: "Testing",
    academic_year_start: "",
    academic_year_end: "",
    post_request_start: "",
    post_request_end: "",
    transfer_request_start: "",
    transfer_request_end: "",
    internal_transfer_assessment_start: "",
    internal_transfer_assessment_end: "",
    external_transfer_assessment_start: "",
    external_transfer_assessment_end: "",
    teacher_recruitment_start: "",
    teacher_recruitment_end: "",
    status: "1",
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prevState) => ({
      ...prevState,
      [name]: value,
    }));
  };

  const onSubmitHandler = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    try {
      setAxiosToken();
      await axios.post(`${APP_URL}/academiccalendars`, formData);
      alert("New Calendar Submitted Successful");
      setFormData({
        academic_year_name: "",
        academic_year_description: "Testing",
        academic_year_start: "",
        academic_year_end: "",
        post_request_start: "",
        post_request_end: "",
        transfer_request_start: "",
        transfer_request_end: "",
        internal_transfer_assessment_start: "",
        internal_transfer_assessment_end: "",
        external_transfer_assessment_start: "",
        external_transfer_assessment_end: "",
        teacher_recruitment_start: "",
        teacher_recruitment_end: "",
        status: "1",
      });
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <Container title="Add New Academic" subTitle="">
      <div className="relative flex flex-col bg-white shadow-lg rounded-md p-5">
        <form onSubmit={onSubmitHandler}>
          <div className="">
            <TextInput
              onChange={(e) => handleChange(e)}
              error={""}
              placeholder="Academic Year Name"
              value={formData.academic_year_name}
              type="text"
              name="academic_year_name"
              label="Academic Year Name"
            />
          </div>
          {/* Academic */}
          <div className="">
            <h1>Academic</h1>
            <div className="inline-flex gap-3 w-full">
              <TextInput
                onChange={(e) => handleChange(e)}
                error={""}
                placeholder="Academic Start Date"
                value={formData.academic_year_start}
                type="date"
                name="academic_year_start"
                label="Academic Start Date"
              />
              <TextInput
                onChange={(e) => handleChange(e)}
                error={""}
                placeholder="Academic End Date"
                value={formData.academic_year_end}
                type="date"
                name="academic_year_end"
                label="Academic End Date"
              />
            </div>
          </div>
          {/* Request */}
          <div className="">
            <h2>Request Post</h2>
            <div className="inline-flex gap-3  w-full">
              <TextInput
                onChange={(e) => handleChange(e)}
                error={""}
                placeholder="Post Request Start Date"
                value={formData.post_request_start}
                type="date"
                name="post_request_start"
                label="Post Request Start Date"
              />
              <TextInput
                onChange={(e) => handleChange(e)}
                error={""}
                placeholder="Post Request End Date"
                value={formData.post_request_end}
                type="date"
                name="post_request_end"
                label="Post Request End Date"
              />
            </div>
          </div>
          {/* Transfer */}
          <div className="">
            <h2>Transfer Request</h2>
            <div className="inline-flex gap-3  w-full">
              <TextInput
                onChange={(e) => handleChange(e)}
                error={""}
                placeholder="Transfer Request Start Date"
                value={formData.transfer_request_start}
                type="date"
                name="transfer_request_start"
                label="Transfer Request Start Date"
              />
              <TextInput
                onChange={(e) => handleChange(e)}
                error={""}
                placeholder="Transfer Request End Date"
                value={formData.transfer_request_end}
                type="date"
                name="transfer_request_end"
                label="Transfer Request End Date"
              />
            </div>
          </div>
          {/* Internal Assessment */}
          <div className="">
            <h2>Internal Assessment</h2>
            <div className="inline-flex gap-3  w-full">
              <TextInput
                onChange={(e) => handleChange(e)}
                error={""}
                placeholder="Internal Transfer Start Date"
                value={formData.internal_transfer_assessment_start}
                type="date"
                name="internal_transfer_assessment_start"
                label="Internal Transfer Start Date"
              />
              <TextInput
                onChange={(e) => handleChange(e)}
                error={""}
                placeholder="Internal Transfer End Date"
                value={formData.internal_transfer_assessment_end}
                type="date"
                name="internal_transfer_assessment_end"
                label="Internal Transfer End Date"
              />
            </div>
          </div>
          {/* External Assessment */}
          <div className="">
            <h2>External Assessment</h2>
            <div className="inline-flex gap-3  w-full">
              <TextInput
                onChange={(e) => handleChange(e)}
                error={""}
                placeholder="External Transfer Start Date"
                value={formData.external_transfer_assessment_start}
                type="date"
                name="external_transfer_assessment_start"
                label="External Transfer Start Date"
              />
              <TextInput
                onChange={(e) => handleChange(e)}
                error={""}
                placeholder="External Transfer End Date"
                value={formData.external_transfer_assessment_end}
                type="date"
                name="external_transfer_assessment_end"
                label="External Transfer End Date"
              />
            </div>
          </div>
          {/* teacher recruitment */}
          <div className="">
            <h2>Teacher Recruitment</h2>
            <div className="inline-flex gap-3  w-full">
              <TextInput
                onChange={(e) => handleChange(e)}
                error={""}
                placeholder="Teacher Recruitment Start Date"
                value={formData.teacher_recruitment_start}
                type="date"
                name="teacher_recruitment_start"
                label="Teacher Recruitment Start Date"
              />
              <TextInput
                onChange={(e) => handleChange(e)}
                error={""}
                placeholder="End Date"
                value={formData.teacher_recruitment_end}
                type="date"
                name="teacher_recruitment_end"
                label="Teacher Recruitment End Date"
              />
            </div>
          </div>
          <button
            className=" bg-accent-800 text-white hover:bg-accent-900 font-bold uppercase text-sm px-6 py-3 rounded shadow outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
            type="submit"
            onClick={() => null}
          >
            Save Changes
          </button>
        </form>
      </div>
    </Container>
  );
};

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const AddNewAcademicForm = connect(
  mapStateToProps,
  {}
)(_AddNewAcademicForm);
